body,
html {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 100%;
}

#root {
  height: 100%;
}

.component-area,
.canvas-area {
  background-color: rgba(0, 0, 0, 0.7);
  border: solid 1px rgba(255, 255, 255, 0.2);
  -webkit-box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
          box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
  border-radius: 20px;
  overflow: hidden;
  color: white;
}

.component-area {
  padding: 20px;
  -ms-flex: 1 1;
      flex: 1 1;
  margin-right: 20px;
  margin-left: 20px;
  height: calc(100% - 80px);
}
.canvas-area {
  -ms-flex: 4 1;
      flex: 4 1;
  width: 100%;
  margin-right: 20px;
  height: calc(100% - 40px);
}



